import React from 'react'
import './style.css';


export default function index() {
    return (
         <section className="landing">
            <div className="row vertical-center">
                <div className="col-lg-8 text-left pb-5 aligncenter">
                    <h1 className="display-2">Sourav Mangla</h1>
                    <p className="lead pl-2">
                      Extremely motivated to constantly develop my skills &amp; grow professionally
                    </p>
                </div>
                <div className="col-lg-4 text-center my-auto pb-5 rounded-circle">
                  <img className="img-fluid rounded-circle" src={process.env.PUBLIC_URL + '/mypic.jpg'} alt={"myimg"}/>
                  </div>
            </div>
        </section>    
    )
}
